import { useCallback, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import CertificateRequest from "./CertificateRequest";
import style from "../../FinanceManager/FinanceManager.module.css";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CertificateLogs from "../CertificateLogs/CertificateLogs";
import CreateButton from "../../../Ui/Button/CreateButton";
import InstituteStudent from "../../InstituteStudent/InstituteStudent";
const CertificateRequestConfig = ({ instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [step, setStep] = useState("");

  const onNavigate = useCallback(() => {
    let url = "";

    url = `/q/${params.username}/member/staff/admission`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  const onClose = () => {
    setStep("");
  };

  const onSelectSingleStudent = (val) => {
    navigate(`/q/${params.username}/student/certificate/form`, {
      state: {
        studentId: val?._id,
        staffId: getQuery.state?.sid,
        is_bonafide: true,
      },
    });
  };
  // console.info("sdjfvdsf", getQuery.state);

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.2rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{t("issue_certificate")}</h6>
            </div>
            <CreateButton
              label="issue_bonafide"
              onAction={() => setStep(1)}
              customStyle={{
                margin: "0",
                padding: "0.3rem 1rem",
              }}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={`/q/${params.username}/member/certificate?a=request${tab.urlKey}`}
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          {searchParams.get("n") === "history" ? (
            <CertificateLogs instituteId={instituteId} />
          ) : (
            <CertificateRequest instituteId={instituteId} />
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {step === 1 ? (
        <InstituteStudent
          title={t("select_student")}
          onClose={onClose}
          instituteId={instituteId}
          onSelectStudent={onSelectSingleStudent}
          isSearchOnly
        />
      ) : null}
    </>
  );
};
export default CertificateRequestConfig;

const tabList = [
  {
    label: "request",
    id: "request",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "history",
    id: "history",
    urlKey: "&n=history",
    groupIs: ["history"],
  },
];
