import { useEffect, useState } from "react";
import {
  useCertificateBonafideDublicate,
  useCertificateLeavingFormSetting,
} from "../../../Hooks/ApiHooks/CertificateManager/certificate-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";

import Switch from "@mui/material/Switch";
const label = { inputProps: { "aria-label": "Switch demo" } };

const CertificateSetting = ({ instituteId, masterData }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    certificate_leaving_form_student: "",
    is_dublicate_bonafide: false,
  });

  const [certificateLeavingFormSetting] = useCertificateLeavingFormSetting();
  const [certificateBonafideDublicate] = useCertificateBonafideDublicate();
  useEffect(() => {
    if (masterData?.certificate_leaving_form_student) {
      setState((prev) => ({
        ...prev,
        certificate_leaving_form_student:
          masterData?.certificate_leaving_form_student,
      }));
    }
    if (masterData?.is_dublicate_bonafide) {
      setState((prev) => ({
        ...prev,
        is_dublicate_bonafide:
          masterData?.is_dublicate_bonafide === "Yes" ? true : false,
      }));
    }
  }, [
    masterData?.certificate_leaving_form_student,
    masterData?.is_dublicate_bonafide,
  ]);
  const onSelectDropdown = (e, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: e,
    }));
  };

  const onSave = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      certificateLeavingFormSetting({
        id: instituteId,
        bodyParameter: state,
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onDublicateCopyAddOn = (e) => {
    if (instituteId) {
      setState((prev) => ({
        ...prev,
        is_dublicate_bonafide: e.target.checked,
      }));
      setDisabled((pre) => !pre);
      certificateBonafideDublicate({
        id: instituteId,
        bodyParameter: {
          is_dublicate_bonafide: e.target.checked ? "Yes" : "No",
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  // console.info("masterData", masterData);
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("setting")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
        <div
          className={style.fm_header_container}
          style={{
            gap: "2%",
            alignItems: "flex-end",
          }}
        >
          <CustomSelect
            selectLabel={t("leaving_certificate_form")}
            selectedValue={t("leaving_certificate_form_placeholder")}
            defalutValue={state.certificate_leaving_form_student}
            onClick={(val) =>
              onSelectDropdown(val, "certificate_leaving_form_student")
            }
            options={["Yes", "No"]}
            customStyleContainer={{
              width: "49%",
            }}
            customListStyle={{
              position: "absolute",
              width: "100%",
              zIndex: "1",
            }}
          />
          <CreateButton
            label="save"
            onAction={onSave}
            customStyle={{
              width: "49%",
              margin: "0",
              padding: "0.5rem 1rem",
            }}
          />
        </div>
        <BorderBottom />
        <div>
          <Switch
            {...label}
            checked={state.is_dublicate_bonafide}
            onChange={onDublicateCopyAddOn}
          />
          <span>{t("bonafide_dublicate_copy")}</span>
        </div>
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </UserMemberContainerWrapper>
  );
};

export default CertificateSetting;
